import { getRawPromise } from '../Target';

class Children extends Array {
  register(child) {
    const handler = () => {
      const index = this.indexOf(child);
      if (index >= 0) {
        this.splice(index, 1);
      }
    };

    getRawPromise(child).then(handler, handler);
    this.push(child);
  }
}

export default Children;



